/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
}

p {
  font-family: "Barlow", sans-serif;
  font-size: 1.8rem;
}

.container {
  padding: 0.8rem 0;
  width: 90%;
  margin: 0 auto;
  max-width: 120rem;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.seccion {
  padding: 2rem 0;
}

h1 {
  font-size: 3.4rem;
  font-family: "Fraunces", serif;
}

h2 {
  font-size: 3rem;
  font-family: "Fraunces", serif;
}

h3 {
  font-size: 2.4rem;
}

a {
  text-decoration: none;
}

[class$=__heading] {
  margin: 2rem 0;
}

.header {
  background-image: url(../img/mobile/image-header.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
@media (min-width: 768px) {
  .header {
    background-image: url(../img/desktop/image-header.jpg);
  }
}
.header__info {
  height: 40rem;
  text-align: center;
  padding-top: 6rem;
}
@media (min-width: 1024px) {
  .header__info {
    height: 60rem;
    padding-top: 4rem;
  }
}
.header__title {
  color: white;
  text-transform: uppercase;
  letter-spacing: 1rem;
  font-family: "Fraunces", serif;
  font-weight: 900;
}
@media (min-width: 1024px) {
  .header__title {
    font-size: 5.5rem;
  }
}
.header__arrow {
  margin: 0 auto;
}

.navbar {
  padding: 3rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navbar__hamburguer {
  cursor: pointer;
}
.navbar__menu {
  position: absolute;
  top: -25rem;
  left: 0;
  right: 0;
  width: 80%;
  margin: 0 auto;
  padding: 2em 1em;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
  transition: 0.7s ease-out;
}
.navbar__menu::before {
  content: "";
  position: absolute;
  border-top: 10px solid transparent;
  border-left: 10px solid transparent;
  border-right: 10px solid #fff;
  border-bottom: 10px solid #fff;
  top: -2rem;
  right: 0;
}
.navbar__menu--active {
  top: 8rem;
  transition: 0.7s ease;
}
.navbar__link {
  font-family: "Barlow", sans-serif;
  color: #818498;
}
@media (min-width: 1024px) {
  .navbar__link {
    color: white;
  }
}
.navbar__btn {
  color: #23303e;
  background-color: #fad400;
  padding: 1rem 1.5rem;
  border-radius: 2rem;
  font-weight: 600;
  text-transform: uppercase;
  font-family: "Fraunces", serif;
}
@media (min-width: 1024px) {
  .navbar__hamburguer {
    display: none;
  }
  .navbar__menu {
    flex-direction: row;
    gap: 5rem;
    position: unset;
    top: unset;
    width: unset;
    margin: unset;
    padding: 0.2rem 0.1rem;
    background-color: unset;
  }
  .navbar__btn {
    color: #23303e;
    background-color: white;
    padding: 1rem 1.5rem;
    border-radius: 2rem;
    font-weight: 600;
    opacity: 1;
  }
  .navbar__btn:hover {
    color: white;
    background-color: rgba(255, 255, 255, 0.5);
  }
}

.services__grid {
  width: 100%;
  display: grid;
  grid-template-rows: repeat(2, 25rem 40rem);
}
@media (min-width: 768px) {
  .services__grid {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 55rem);
    grid-template-areas: "text1 img1" "img2 text2";
  }
}
.services__img--one {
  background-image: url(../img/mobile/image-transform.jpg);
  background-size: cover;
  background-position: center;
}
@media (min-width: 768px) {
  .services__img--one {
    background-image: url(../img/desktop/image-transform.jpg);
    grid-area: img1;
  }
}
.services__text {
  place-self: center;
  padding: 1rem 2.5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .services__text {
    text-align: start;
    grid-area: text1;
    padding: 5rem;
    max-width: 55rem;
  }
}
@media (min-width: 768px) {
  .services__text--2 {
    grid-area: text2;
  }
}
.services__title {
  color: #23303e;
}
@media (min-width: 1024px) {
  .services__title {
    font-size: 4.2rem;
  }
}
.services__paragraph {
  color: #5a636c;
  line-height: 1.5;
  position: relative;
}
.services__btn {
  font-family: "Fraunces", serif;
  color: #23303e;
  font-family: "Fraunces", serif;
  text-transform: uppercase;
  text-decoration: none;
  position: relative;
  display: flex;
  justify-content: center;
}
.services__btn::after {
  border-radius: 2rem;
  bottom: 0;
  content: " ";
  display: block;
  height: 0.8rem;
  position: absolute;
  width: 33%;
  z-index: -1;
  opacity: 0.5;
  background-color: #fad400;
}
@media (min-width: 768px) {
  .services__btn {
    display: block;
  }
  .services__btn::after {
    width: 24%;
  }
}
.services__img--two {
  background-image: url(../img/mobile/image-stand-out.jpg);
  background-size: cover;
  background-position: center;
}
@media (min-width: 768px) {
  .services__img--two {
    background-image: url(../img/desktop/image-stand-out.jpg);
    grid-area: img2;
  }
}
.services__btn--two {
  font-family: "Fraunces", serif;
  color: #23303e;
  font-family: "Fraunces", serif;
  text-transform: uppercase;
  text-decoration: none;
  position: relative;
  display: flex;
  justify-content: center;
}
.services__btn--two::after {
  border-radius: 2rem;
  bottom: 0;
  content: " ";
  display: block;
  height: 0.8rem;
  position: absolute;
  width: 33%;
  z-index: -1;
  opacity: 0.5;
  background-color: #fe7867;
}
@media (min-width: 768px) {
  .services__btn--two {
    display: block;
  }
  .services__btn--two::after {
    width: 24%;
  }
}

.design {
  display: grid;
  grid-auto-rows: 56rem;
}
@media (min-width: 768px) {
  .design {
    grid-template-columns: repeat(2, 1fr);
  }
}
.design__section {
  display: grid;
  align-content: end;
  background-position: center;
  background-size: cover;
  background-image: url("../img/mobile/image-graphic-design.jpg");
  padding: 2.5rem 1.5rem;
  text-align: center;
  color: #25564b;
}
@media (min-width: 768px) {
  .design__section {
    background-image: url("../img/desktop/image-graphic-design.jpg");
  }
}
.design__section--two {
  background-image: url("../img/mobile/image-photography.jpg");
  color: #19536b;
}
@media (min-width: 768px) {
  .design__section--two {
    background-image: url("../img/desktop/image-photography.jpg");
  }
}
.design__paragraph {
  line-height: 1.5;
  font-size: 1.6rem;
  margin: 0;
}
@media (min-width: 768px) {
  .design__paragraph {
    padding: 0 3rem;
  }
}
@media (min-width: 1024px) {
  .design__paragraph {
    padding: 0 15rem 2rem;
    max-width: 66rem;
  }
}

.testimonials {
  text-align: center;
}
.testimonials__title {
  text-transform: uppercase;
  color: #a7abae;
  font-size: 1.7rem;
  letter-spacing: 0.5rem;
  margin: 5rem 0;
}
.testimonials__grid {
  display: grid;
  gap: 6rem;
}
@media (min-width: 768px) {
  .testimonials__grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
.testimonials__item {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}
.testimonials__img {
  width: 8rem;
  height: 8rem;
  object-fit: cover;
  border-radius: 50%;
  margin: 0 auto;
}
.testimonials__paragraph {
  line-height: 1.5;
  color: #5a636c;
}
.testimonials__name {
  margin: 0;
  color: #23303e;
  font-family: "Fraunces", serif;
}
.testimonials__job {
  margin: 1rem 0 0 0;
  color: #a7abae;
  font-size: 1.5rem;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .gallery {
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 35rem;
  }
}
.gallery__img {
  background-position: center;
  background-size: cover;
  width: 100%;
  aspect-ratio: 1/1;
}
.gallery__img--one {
  background-image: url("../img/mobile/image-gallery-milkbottles.jpg");
}
@media (min-width: 768px) {
  .gallery__img--one {
    background-image: url("../img/desktop/image-gallery-milkbottles.jpg");
    background-position: bottom;
  }
}
.gallery__img--two {
  background-image: url("../img/mobile/image-gallery-orange.jpg");
}
@media (min-width: 768px) {
  .gallery__img--two {
    background-image: url("../img/desktop/image-gallery-orange.jpg");
  }
}
.gallery__img--three {
  background-image: url("../img/mobile/image-gallery-cone.jpg");
}
@media (min-width: 768px) {
  .gallery__img--three {
    background-image: url("../img/desktop/image-gallery-cone.jpg");
    background-position: bottom;
  }
}
.gallery__img--four {
  background-image: url("../img/mobile/image-gallery-sugar-cubes.jpg");
}
@media (min-width: 768px) {
  .gallery__img--four {
    background-image: url("../img/desktop/image-gallery-sugarcubes.jpg");
  }
}

.footer {
  background-color: #90D4C5;
  padding: 5rem 0;
}
.footer__container {
  display: flex;
  flex-direction: column;
  gap: 5rem;
  align-items: center;
}
.footer__logo {
  margin-top: 2rem;
  background-color: #90D4C5;
}
.footer__nav {
  display: flex;
  gap: 5rem;
}
.footer__link {
  font-family: "Barlow", sans-serif;
  color: #458c7e;
}
.footer__link:hover {
  color: white;
}
.footer__card {
  display: flex;
  gap: 3rem;
  padding: 3rem 0;
}
.footer__icon {
  color: #25564b;
}
.footer__icon:hover {
  color: white;
}